<template>
  <!-- nav start -->
  <div class="fit">
    <div class="db-navbar dark">
      <div>
        <router-link :to="{name:'editor'}">
          <img src="../assets/MarkdownLogo_mini.png" class="logo">
        </router-link>
      </div>
      <div class="rightbar">
        <el-dropdown trigger="click" class="b">
          <span class="el-dropdown-link" style="cursor: pointer;">用户名称</span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>我的消息</el-dropdown-item>
            <el-dropdown-item>设置</el-dropdown-item>
            <el-dropdown-item divided>退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <!-- nav end -->
    <!-- sidebar start -->
    <div class="db-body">
      <div class="sidebar dark">
        <el-menu class="dark" theme="dark" router>
          <el-menu-item index="/dashboard/posts"><i class="el-icon-z-article"></i>文章管理</el-menu-item>
          <el-menu-item index="/dashboard/images"><i class="el-icon-z-tupian"></i>图片管理</el-menu-item>
          <el-menu-item index="/dashboard/category"><i class="el-icon-z-tupian"></i>分类管理</el-menu-item>
        </el-menu>
      </div>
      <main class="main">
        <router-view></router-view>
        <!-- <div style="width:100%;background-color:red;height:10px;"></div> -->
      </main>
    </div>
    <!-- sidebar end -->
  </div>
</template>
<script>
export default {
  data() {
    return {

    }
  }
}
</script>
<style scoped>
.fit {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: stretch;
  justify-content: space-between;
}

.b {
  border: 1px solid red;
}

.dark {
  /*background-color: #324157;*/
  background-color: rgb(68, 68, 68);
}

.light {
  background-color: #eef1f6;
}

.db-navbar {
  height: 40px;
}

.db-navbar .logo {
  height: 34px;
  width: 34px;
  float: left;
  margin: 3px 10px;
}

.db-navbar .rightbar {
  text-align: right;
  padding-right: 35px;
  height: 100%;
}

.db-navbar .el-dropdown {
  height: 100%;
  width: 100px;
  color: white;
}

.db-navbar .el-dropdown span {
  line-height: 40px;
  display: block;
  text-align: center;
  font-family: '微软雅黑', 'Consolas';
}

.db-body {
  display: flex;
  align-items: stretch;
  height: calc(100% - 40px);
  width: 100%;
}

.db-body .sidebar {
  width: 200px;
  padding: 10px 0;
}

.db-body .main {
  width: calc(100% - 200px);
}


/*.rightbar .head {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  margin: 10px 0px 10px 10px;
  float: right;
}*/
</style>



